<template>
    <div class="buttons-div">
        <template v-for="(item,index) in YMD">
            <p><el-button type="success" plain @click="JTthisWeekI(item.value)" :autofocus="true">{{item.name}}</el-button></p>
            <p v-if="!item.isEnd">——</p>
        </template>
        <!-- <p><el-button type="success" plain class="p-button">日</el-button></p>
        <p>——</p>
        <p><el-button type="success" plain class="p-button">月</el-button></p>
        <p>——</p>
        <p><el-button type="success" plain class="p-button">年</el-button></p> -->
    </div>
</template>
<script>
    export default {
        data(){
            return {
                YMD:[{name:'日',value:'thisday',isEnd:false},{name:'月',value:'month',isEnd:false},{name:'年',value:'year',isEnd:true}],
            }
        },

        mounted() {

        },

        methods: {
            JTthisWeekI(type){
                if(type === 'thisday'){
                    this.$store.state.JTweekTextI = 1;
                }else if(type === 'month'){
                    this.$store.state.JTweekTextI = 2;
                    this.$store.state.JTweekTextII = 2;
                }else if(type === 'year'){
                    this.$store.state.JTweekTextI = 3;
                    this.$store.state.JTweekTextII = 3;
                }else{
                    this.$store.state.JTweekTextI = 1;
                    this.$store.state.JTweekTextII = 2;
                }
            },

        }
    }
</script>

<style scoped lang="less" type="text/less">
    .buttons-div{
        height: 90%;
        p{
            font-size: 20px;
            height: 5%;
            text-align: center;
        }
        .p-button{
            font-size: 20px;
        }
    }
</style>



